@tailwind base;
@tailwind components;
@tailwind utilities;


html {
  font-size: 62.5%;
}

body {
	height: 100vh;
	position: relative;
  background-color: #e9c46a;
}

.svg {
  position: absolute;
  transform: rotate(-0.25turn);
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: center;
}

.circle {
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
}

.animate-10 {
  animation: stroke ease-out forwards;
  animation-duration: 46s;
}

.animate-20 {
  animation: stroke ease-out forwards;
  animation-duration: 104s;
}

@keyframes stroke {
  to {
    stroke-dashoffset: 0;
  }
}



@media screen and (min-width: 760px) {
  html {
    font-size: 80%;
  }

  .animate-10 {
    animation-duration: 44s;
  }

  .animate-20 {
    animation-duration: 100s;
  }
}

@media screen and (min-width: 1000px) {
  html {
    font-size: 110%;
  }

  .animate-10 {
    animation-duration: 31s;
  }

  .animate-20 {
    animation-duration: 68s;
  }
}

@media screen and (max-width: 400px) {
  html {
    font-size: 90%;
  }

  .animate-10 {
    animation-duration: 40s;
  }

  .animate-20 {
    animation-duration: 87s;
  }
}
